@import "../../assets/scss/colors.scss";

.grupos {
  background-color: $color-light;
  box-shadow: $box-shadow;
  padding: 1rem;
  border-radius: $border-radius;
  height: 60rem;

  &__container {
    clear: both;
  }

  h1 {
    color: $color-grupos;
    margin: 0.5rem 0 0 0;
  }

  a {
    text-decoration: none;
    color: $color-grupos;
    text-align: end;
    font-weight: bold;
    float: right;
    margin: 1.3rem 2rem 0 0;
  }

  a[class$="plus"] {
    font-size: 1.3rem;
    font-weight: lighter;
    margin: 0 2rem 1.5rem 0;
  }
}

@media (min-width: 40rem) {
  .grupos {
    height: 37rem;
    &__container {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-around;
      align-items: center;
    }
  }
}

@media screen and (min-width: 62.5rem) {
  .grupos {
    height: 37rem;
    width: 65%;
    margin: 0;
    display: inline-block;

    &__container {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-around;
      align-items: center;
    }
  }
}
